<template>
    <div>
        <b-breadcrumb>
            <b-breadcrumb-item>{{ $t('common.youAreHere').toUpperCase() }}</b-breadcrumb-item>
            <b-breadcrumb-item active>
                {{$t('menuCustomerCreare')}}
            </b-breadcrumb-item>
        </b-breadcrumb>
        <p v-if="$route.params.id">{{ $route.params.id }}</p>
        <button @click="validationOn = !validationOn">Validation: {{ validationOn }}</button>
        <b-row>
            <b-col lg="12" xs="12">
                
                <form-wizard shape="tab"
                             color="#3498db"
                             title=""
                             subtitle=""
                             ref="formWizard"
                >
                    <!-- <b-progress class="progress-xs" variant="gray-light" :value="progress" :max="4" /> -->
                    <b-button slot="prev" variant="primary" @click="progress--">
                        <i class="fa fa-caret-left" /> {{$t('menuCustomerCreatePrevious')}}
                    </b-button>
                    <b-button slot="next" variant="primary" @click.prevent="onNext">
                        {{$t('menuCustomerCreateNext')}} <i class="fa fa-caret-right" />
                    </b-button>
                    <b-button slot="finish" variant="success" @click="checkNext">
                        {{$t('menuCustomerCreateFinish')}} <i class="fa fa-check" />
                    </b-button>
                    <tab-content :title="$t('menuCustomerCreateDetailsTab')" :before-change="checkNext">
                        <b-form>
                            <b-row>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateFirstName')"
                                                  label-for="firstname"
                                                  description="">
                                            <b-form-input id="firstname" name="firstname" type="text" 
                                                v-model="customer.custFirstname" :placeholder="$t('placeholders.name')" 
                                                v-validate="'required|alpha'" data-vv-scope="step1" 
                                                :class="{'form-control': true, 'is-invalid': errors.has('step1.firstname')}"
                                            />
                                            <span class="validation-error" v-show="errors.has('step1.firstname')">{{ errors.first('step1.firstname') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateLastName')"
                                                  label-for="lastname"
                                                  description="">
                                        <b-form-input id="lastname" name="lastname" type="text" 
                                            v-model="customer.custLastname" :placeholder="$t('placeholders.lastname')"
                                            v-validate="'required|alpha'" data-vv-scope="step1"
                                            :class="{'form-control': true, 'is-invalid': errors.has('step1.lastname')}"
                                        />
                                        <span class="validation-error" v-show="errors.has('step1.lastname')">{{ errors.first('step1.lastname') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateEmail')"
                                                  label-for="email"
                                                  description="">
                                        <b-form-input id="email" type="email" name="email"
                                            v-model="customer.custMail" :placeholder="$t('placeholders.email')"
                                            v-validate="'required|email'" data-vv-scope="step1"
                                            :class="{'form-control': true, 'is-invalid': errors.has('step1.email')}"
                                        />
                                        <span class="validation-error" v-show="errors.has('step1.email')">{{ errors.first('step1.email') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreatePhone')"
                                                  label-for="phone"
                                                  description="">
                                        <b-form-input id="phone" type="text" name="phone"
                                            v-model="customer.custPhone" :placeholder="$t('placeholders.phone')"
                                            v-validate="{ required: true}" v-mask="'+### ### ### ###'" data-vv-scope="step1"
                                            :class="{'form-control': true, 'is-invalid': errors.has('step1.phone')}"
                                        />
                                        <span class="validation-error" v-show="errors.has('step1.phone')">{{ errors.first('step1.phone') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('tableHeadings.dateOfBirth')"
                                                  label-for="dateOfBirth"
                                                  description="">
                                        <b-form-input id="dateOfBirth" type="date" name="dateOfBirth"
                                            v-model="customer.custBirthdate"
                                            v-validate="'required'" data-vv-scope="step1"
                                            :class="{'form-control': true, 'is-invalid': errors.has('step1.dateOfBirth')}"
                                        />
                                        <span class="validation-error" v-show="errors.has('step1.dateOfBirth')">{{ errors.first('step1.dateOfBirth') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                        </b-form>
                        <b-row>
                                <b-col lg="2" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateAddress')" label-for="street" description="">
                                        <b-form-input id="street" name="street" :placeholder="$t('placeholders.street')"
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step3.street')}"
                                                v-validate="'required'" data-vv-scope="step3" data-vv-as="Street" 
                                                v-model="customer.custStreet" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step3.street')">{{ errors.first('step3.street') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="2" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateAddressHouse')" label-for="house" description="">
                                        <b-form-input id="house" name="house" :placeholder="$t('placeholders.house')"
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step3.house')}"
                                                v-validate="'required'" data-vv-scope="step3" data-vv-as="House"  
                                                v-model="customer.custHouse" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step3.house')">{{ errors.first('step3.house') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="2" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateAddressFlat')" label-for="flat" description="">
                                        <b-form-input id="flat" name="flat" :placeholder="$t('placeholders.flat')"
                                                type="text" 
                                                :class="{'form-control': true, 'is-invalid': errors.has('step3.flat')}"
                                                v-validate="'required'" data-vv-scope="step3" data-vv-as="Flat"
                                                v-model="customer.custFlat" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step3.flat')">{{ errors.first('step3.flat') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="2" xs="12">
                                    <b-form-group :label="$t('tableHeadings.postalCode')" label-for="postal" description="">
                                        <b-form-input id="postal" name="postal" :placeholder="$t('placeholders.postalCode')"
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step3.postal')}"
                                                v-validate="'required'" data-vv-scope="step3" data-vv-as="Postal code" 
                                                v-model="customer.custCitycode" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step3.postal')">{{ errors.first('step3.postal') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="2" xs="12">
                                    <b-form-group :label="$t('tableHeadings.city')" label-for="city" description="">
                                        <b-form-input id="city" name="city" :placeholder="$t('placeholders.city')"
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step3.city')}"
                                                v-validate="'required'" data-vv-scope="step3" data-vv-as="City" 
                                                v-model="customer.custCity" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step3.city')">{{ errors.first('step3.city') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="2" xs="12">
                                    <b-form-group :label="$t('menuCustomerCreateAddressZone')" label-for="province">
                                        <v-select searchable
                                                id="province" name="province"
                                                :class="{'editableSelect' : true, 'invalid-select': errors.has('step3.province')}"
                                                v-validate="'required'" data-vv-scope="step3" data-vv-as="Province"
                                                v-model="customer.custZone"
                                                :options="this.zones"
                                        />
                                        <b-button @click="showDropdownEditor('DDL/Zones')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button>
                                        <span class="validation-error" v-show="errors.has('step3.province')">{{ errors.first('step3.province') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                    </tab-content>

                    <tab-content :title="$t('menuCustomerCreatePatientTab')" :before-change="checkNext">
                        <b-form>
                            <b-row>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateName')" label-for="patientName" description="">
                                        <b-form-input id="patientName" type="text" name="petname" :placeholder="$t('placeholders.petName')"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step2.petname')}"
                                                v-validate="'required'" data-vv-scope="step2" data-vv-as="Petname"
                                                v-model="patient.name"  
                                        />
                                        <span class="validation-error" v-show="errors.has('step2.petname')">{{ errors.first('step2.petname') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateChip')" label-for="patientChip" description="">
                                        <b-form-input id="patientChip" name="patientChip" :placeholder="$t('placeholders.chip')"
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step2.patientChip')}"
                                                v-validate="'numeric'" data-vv-scope="step2" data-vv-as="Chip"
                                                v-model="patient.chip" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step2.patientChip')">{{ errors.first('step2.patientChip') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('tableHeadings.dateOfBirth')" label-for="patientDateOfBirth" description="">
                                        <b-form-input id="patientDateOfBirth" name="patientDateOfBirth" 
                                                type="date"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step2.patientDateOfBirth')}"
                                                v-validate="''" data-vv-scope="step2" data-vv-as="Birthdate"
                                                v-model="patient.birthdate" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step2.patientDateOfBirth')">{{ errors.first('step2.patientDateOfBirth') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateWeight')" label-for="patientWeight" description="">
                                        <b-form-input id="patientWeight" name="patientWeight" :placeholder="$t('placeholders.weight')" 
                                                type="text"
                                                :class="{'form-control': true, 'is-invalid': errors.has('step2.patientWeight')}"
                                                v-validate="{ decimal: [3, ',']}" data-vv-scope="step2" data-vv-as="Weight"
                                                v-model="patient.weight" 
                                        />
                                        <span class="validation-error" v-show="errors.has('step2.patientWeight')">{{ errors.first('step2.patientWeight') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateSex')" label-for="patientGender">
                                        <v-select searchable
                                                  id="patientGender" name="patientGender"
                                                  :class="{'editableSelect' : true, 'invalid-select': errors.has('step2.patientGender')}"
                                                  v-validate="'required'" data-vv-scope="step2" data-vv-as="Gender"
                                                  v-model="patient.gender"
                                                  :options="this.genders" 
                                        />
                                        <b-button @click="showDropdownEditor('DDL/Plec')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button>
                                        <span class="validation-error" v-show="errors.has('step2.patientGender')">{{ errors.first('step2.patientGender') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateColor')" label-for="patientColor">
                                        <v-select searchable
                                                  id="patientColor" name="patientColor"
                                                  :class="{'editableSelect' : true, 'invalid-select': errors.has('step2.patientColor')}"
                                                  v-validate="'required'" data-vv-scope="step2" data-vv-as="Color"
                                                  v-model="patient.color"
                                                  :options="this.colors"
                                        />
                                        <b-button @click="showDropdownEditor('DDL/Colors')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button>
                                        <span class="validation-error" v-show="errors.has('step2.patientColor')">{{ errors.first('step2.patientColor') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateType')" label-for="patientType">
                                        <v-select searchable
                                                  id="patientType" name="patientType"
                                                  :class="{'editableSelect' : true, 'invalid-select': errors.has('step2.patientType')}"
                                                  v-validate="'required'" data-vv-scope="step2" data-vv-as="Type"
                                                  v-model="patient.type"
                                                  :options="this.types"
                                        />
                                        <b-button @click="showDropdownEditor('DDL/Types')" class="editButton btn-outline-warning"> 
                                            <span class="glyphicon glyphicon-pencil mr-xs mb-xs"></span>
                                        </b-button>
                                        <span class="validation-error" v-show="errors.has('step2.patientType')">{{ errors.first('step2.patientType') }}</span>
                                    </b-form-group>
                                </b-col>
                                <b-col lg="3" xs="12">
                                    <b-form-group :label="$t('menuPatientCreateRace')" label-for="patientRace">
                                        <v-select searchable
                                                  id="patientRace" name="patientRace"
                                                  :class="{'invalid-select': errors.has('step2.patientRace')}"
                                                  v-validate="'required'" data-vv-scope="step2" data-vv-as="Race"
                                                  v-model="patient.race"
                                                  :options="['pobierana z API', 'po wybraniu gatunku']"
                                        />
                                        <span class="validation-error" v-show="errors.has('step2.patientRace')">{{ errors.first('step2.patientRace') }}</span>
                                    </b-form-group>
                                </b-col>
                            </b-row>
                            <b-col lg="3" xs="12">
                                <b-form-group class="abc-checkbox abc-checkbox-primary">
                                    <input type="checkbox"
                                           id="castrated-check"
                                           v-model="patient.castrated" />
                                    <label for="castrated-check">{{$t('menuPatientCreateCastrated')}}</label>
                                </b-form-group>
                            </b-col>
                        </b-form>
                    </tab-content>
                    
                <tab-content :title="$t('menuCustomerCreatePermitTab')">
                    <fieldset>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <div class="validation-error" v-show="errors.has('step4.agree1')">{{ errors.first('step4.agree1') }}</div>
                            <input type="checkbox" name="agree1" id="checkbox-primary"
                                    v-model="customer.custAgree1" 
                                    v-validate="'required'" data-vv-scope="step4"
                            />
                            <label for="checkbox-primary"> {{$t('menuPatientCreatePermit1')}}</label>
                            
                        </b-form-group>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <input type="checkbox"
                                id="checkbox-primary1"
                                v-model="customer.custAgree2" />
                            <label for="checkbox-primary1">{{$t('menuPatientCreatePermit2')}}</label>
                        </b-form-group>
                        <b-form-group class="abc-checkbox abc-checkbox-primary">
                            <input type="checkbox"
                                id="checkbox-primary2"
                                v-model="customer.custAgree3" />
                            <label for="checkbox-primary2">{{$t('menuPatientCreatePermit3')}}</label>
                        </b-form-group>
                    </fieldset>
                </tab-content>
            </form-wizard>
            <!--</Widget>-->
            </b-col>
        </b-row>
        <dropdown-edit-modal @closed="hideDropdownEditor"
            :showMe="this.commonShit.dropdownEditShow" 
            :dropdown="this.commonShit.dropdownToEdit"
        />
    </div>
</template>

<script>
import Vue from 'vue';
import vSelect from 'vue-select';
import Widget from '@/components/Widget/Widget';
import DropdownEditModal from "@/components/Custom/DropdownEditModal.vue"
import axios from 'axios';
import { axiosMixin } from "@/core/axiosMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';
// import vSelect from '@/components/vue-select';
export default {
    components: { Widget, vSelect, DropdownEditModal },
    mixins: [axiosMixin],
    data: function() {
        return {
            commonShit: {
                pageMainApi: 'customer',
                editMode: false,
                dropdownEditShow: false,
                dropdownToEdit: '',
            },
            customer: {
                custFirstname: '',
                custLastname: '',
                custMail: '',
                custPhone: '',
                custBirthdate: '',
                custStreet: '',
                custHouse: '',
                custFlat: '',
                custCitycode: '',
                custCity: '',
                custZone: null,
                custAgree1: false,
                custAgree2: false,
                custAgree3: false,
            },
            patient: {
                name: '',
                chip: '',
                birthdate: '',
                weight: '',
                gender: null,
                color: null,
                type: null,
                race: null,
                castrated: false
            },
            progress: 1,
            validationOn: true,
            genders: [],
            colors: [],
            types: [],
            zones: [],
            mockup: {
                colors: [
                    {
                        label:'== offline ==',
                        code: 0, 
                    },
                    {
                        label:'siwek',
                        code: 1, 
                    },
                    {
                        label:'kary',
                        code: 2, 
                    },
                    {
                        label:'biay wilk',
                        code: 3, 
                    },
                ],
                types: [
                    {
                        label:'== offline ==',
                        code: 0, 
                    },
                    {
                        label:'pies',
                        code: 1, 
                    },
                    {
                        label:'kot',
                        code: 2, 
                    },
                    {
                        label:'papuga',
                        code: 3, 
                    },
                ],
                zones: [
                    {
                        label:'== offline ==',
                        code: 0, 
                    },
                    {
                        label:'maopolskie',
                        code: 1, 
                    },
                    {
                        label:'dolnolskie',
                        code: 2, 
                    },
                    {
                        label:'mazowieckie',
                        code: 3, 
                    },
                ],
            },
            defaultSelect: 'SextansA',         
            test : [],
        }
    },

    computed: {
        ...mapGetters([
            'cacheExists',
            'getCache',
            'cacheItemsCount',
            'cacheLastUpdate',
            'cacheGetRowById'
        ])
    },
    
    methods: {
        ...mapActions([
            'cacheReset',
            'cacheFill',
            'cacheAddItem',
            'cacheUpdateItem',
            'cacheSetUpdatedDate',
            'cacheUpdate'
        ]),
        clearForm() {
            this.customer.custFirstname = '';
            this.customer.custLastname = '';
            this.customer.custMail = '';
            this.customer.custPhone = '';
            this.customer.custBirthdate = '';
            this.customer.custStreet = '';
            this.customer.custHouse = '';
            this.customer.custFlat = '';
            this.customer.custCitycode = '';
            this.customer.custCity = '';
            this.customer.custZone = null;
            this.customer.custAgree1 = false;
            this.customer.custAgree2 = false;
            this.customer.custAgree3 = false;
            this.patient.name = '';
            this.patient.chip = '';
            this.patient.birthdate = '';
            this.patient.weight = '';
            this.patient.gender = null;
            this.patient.color = null;
            this.patient.type = null;
            this.patient.race = null;
            this.patient.castrated = false;

            this.$validator.reset();
            this.$refs.formWizard.reset();
            this.progress = 1;
            this.editMode = false;
            
        },
        
        mapCustomerData() {
        var data = JSON.parse(JSON.stringify(this.customer));
        return data;
        },
        async onSubmitNewCustomer() {
            // this.$validator.validateAll();
            var newCustomer = this.mapCustomerData();
            // console.log(newCustomer);
            var result = await this.axiosAddData(this.commonShit.pageMainApi, newCustomer);
            if (this.axeman.success) {
                // ========== ONLY CACHING, NO SERVER WORKLOAD (ASSUMING NO SIMULTANEOUS EDITING)
                if (this.cacheLastUpdate(this.commonShit.pageMainApi) == '1970-01-01T00:00:00') {
                    console.log('Cache not created, not updating');
                } else {
                    console.log('Updating cache: ' + this.commonShit.pageMainApi);
                    this.cacheAddItem({ name: this.commonShit.pageMainApi, data: newCustomer, updateDate: this.axeman.updated});
                }
                this.clearForm();
                Messenger().post(this.$t('customer.added'));
            }
            else {
                console.log(error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
           
        },
        async checkNext() {
            // console.log('Before change validation');
            var isOK = true;
            if (this.validationOn) {
                var result = await this.$validator.validate('step' + this.progress +'.*').then(valid => {
                    if (!valid) {
                        console.log('Step' + + this.progress + ' not valid');
                        isOK = false;
                    } else {
                        if (this.progress == 4 && isOK == true) {
                            this.onSubmitNewCustomer();
                        } else
                            this.progress++;
                    }
                });
            }
            return isOK;
        },
        onNext(e) {
            e.preventDefault();
        },
        showDropdownEditor(dropdown) {
            this.commonShit.dropdownToEdit = dropdown;
            this.commonShit.dropdownEditShow = true;
        },
        hideDropdownEditor() {
            this.commonShit.dropdownEditShow = false;
            this.commonShit.dropdownToEdit = '';
        },
    },

    

    async created () {
         console.log('=== ' + this.commonShit.pageMainApi + ' ===');
      //debugger;
      let route = this.$route.params.id;
        axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;

        if (!this.cacheExists("DDL/Plec")) {
      var result = await this.axiosGetData("DDL/Plec", this.genders);
      this.genders = this.genders.gotFromApi.model;
      this.cacheFill({name: 'DDL/Plec', data:  this.genders, updateDate: '2019-10-10'});
    }
    else {
      this.genders = this.getCache('DDL/Plec');
    }

    // mockup DDLs
    this.cacheFill({name: 'DDL/Colors', data:  this.mockup.colors, updateDate: '2019-10-10'});
    this.cacheFill({name: 'DDL/Types', data:  this.mockup.types, updateDate: '2019-10-10'});
    this.cacheFill({name: 'DDL/Zones', data:  this.mockup.zones, updateDate: '2019-10-10'});
    this.colors = this.getCache('DDL/Colors');
    this.types = this.getCache('DDL/Types');
    this.zones = this.getCache('DDL/Zones');
    },
    
}
</script>

<style scoped>

</style>